import { ToolbarGroup,ToolbarButton } from "@wordpress/components"
import {RichText, BlockControls} from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"

registerBlockType("fitnessblocktheme/fitnessslidetextarea",{
    title:"Fitness Slide Text Area",
    attributes:{
        headingtext:{type:"string"},
        titletext:{type:"string"}
    },
    edit:EditComponent,
    save:SaveComponent
})
function EditComponent(props){
    function handleTextChange(x){
        props.setAttributes({headingtext:x});
    }
    function handleTitleTextChange(x){
        props.setAttributes({titletext:x});
    }
    return (
        <>
            <div className="text-area">
                <RichText allowedFormats={['core/bold','core/italic']} className="font-secondary" tagName="p" value={props.attributes.headingtext} onChange={handleTextChange}/>
                <RichText allowedFormats={['core/bold','core/italic']} tagName="h2" value={props.attributes.titletext} onChange={handleTitleTextChange}/>
            </div>
        </>
    );
}
function SaveComponent(props){
    return (
        <>
            <div className="text-area">
                <RichText.Content tagName="p" className="font-secondary" value={props.attributes.headingtext} />
                <RichText.Content tagName="h2" value={props.attributes.titletext} />
            </div>
        </>
        
    
    );
}